<template>
	<view>
		<form>
			<view class="user-img-box">
				<image :src="imgsrc" mode="aspectFill" class="user-img" @tap="imgclick"></image>
			</view>
			<view class="img-tips">点击头像修改头像</view>

			<view class="user-form">
				<view>用户昵称:</view>
				<input name="username" v-model="form.name" />
			</view>

			<view class="form-btn">
				<button form-type="submit" @click="submit()">修改</button>
				<button type="default" form-type="reset" >重置</button>
			</view>
		</form>
	</view>
</template>

<script>
	import {
		userInfo,
		userChangeInfo
	} from '../../common/api.js'
	export default {
		data() {
			return {
				// userinfo: [],
				form: {
					name: '',
					email:'',
				},
				imgsrc:"/static/76525384_p0.jpg",
			}
		},
		onLoad() {
			if (!this.$u.utils.isLogin(1)) return
			else{
				userInfo().then((res) => {
				// this.userinfo = res.data;
				this.form.name = res.data.username;
				this.form.email = res.data.email;
				// console.log(this.userinfo);
			});
			};
		},
		methods: {
			imgclick(){
				var _this=this;
				uni.chooseImage({
					count:1,
					sizeType: ['compressed'],
					sourceType: ['album'],
					success:function(res){
						_this.imgsrc=res.tempFilePaths[0];
					}
				});
			},
			submit() {
				this.$u.toast('修改成功');
				setTimeout(() => {
					userChangeInfo(this.form.name,this.form.email);
					uni.navigateTo({
						url: '../index/index',
					})
				}, 1500)

					// userInfo().then((res) => {
					// 	// this.userinfo = res.data;
					// 	console.log(res.data.username);
					// 	// console.log(this.userinfo);
					// });
			}
		}
	}
</script>

<style lang="scss" scoped>
	page {
		background-color: #ededed;
	}

	.user-img-box {
		display: flex;
		background-color: #fff;
	}

	.user-img {
		width: 120px;
		height: 120px;
		border-radius: 50%;
		// overflow: hidden;
		margin: 20px auto 20px auto;
	}

	.img-tips {
		background-color: #FFFFFF;
		text-align: center;
		color: gray;
		font-size: 16px;
	}

	.user-form {
		display: flex;
		margin-top: 20px;
		width: 750rpx;
		background-color: white;
		padding: 10px;
	}

	.user-form view:nth-child(1) {
		margin-right: 5px;
	}

	.form-btn {
		margin-top: 20px;
	}

	.form-btn button:nth-child(1) {
		background-color: white;
	}

	.form-btn button:nth-child(2) {
		margin-top: 20px;
		background-color: white;
	}
</style>
